.TechStack {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.TechStack h2 {
  font-size: 3em;
}
.TechStack .technos img {
  height: 300px;
  padding: 50px;
}
@media (max-width: 1025px) {
  .TechStack .technos {
    display: flex;
  }
  .TechStack .technos img {
    height: 100px;
    padding: 0 35px;
  }
}
@media (max-width: 426px) {
  .TechStack .technos {
    display: flex;
  }
  .TechStack .technos img {
    height: 75px;
    padding: 0 18px;
  }
}/*# sourceMappingURL=TechStack.css.map */